.carousel {
  /* background-color: white; */
  margin: 10px 8px;
}
.item_pro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 15%;
  cursor: pointer;
  padding: 3px;
  line-height: 1.5rem;
  /* border: 2px solid red; */
  background-color: white;
}
.pro_image {
  /* min-height: 150px; */
  height: min-content;
}
.pro_image:hover {
  transform: scale(1.05);
}
.caro {
  background-color: white;
}
.head_pro {
  background-color: white;
  margin-bottom: 2px;
  padding: 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.head_pro span {
  font-size: 25px;
  font-weight: 600;
  margin-left: 15px;
}
.view_btn {
  background-color: #2874f0;
  color: white;
  font-size: 15px;
  font-weight: bold;
  border: none;
  padding: 10px 20px;
  margin-right: 15px;
  cursor: pointer;
}
.title_name {
  font-size: 15px;
  font-weight: bold;
}
.price {
  font-size: small;
  color: #388e3c;
}
.tags {
  font-size: 15px;
  color: gray;
  opacity: 0.8;
}
