.categories_item {
  margin: 0px 10px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  box-shadow: 2px 2px rgb(226, 226, 226);
  width: 97.4vw;
  height: 20vh;
}
.category_item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 11%;
  font-size: small;
  /* border: 2px solid black; */
}
.category_item:hover {
  color: #2874f0;
  cursor: pointer;
}
.category_item img {
  height: 80px;
}
