header {
  background: #2874f0;
  height: 9vh;
  display: flex;
  justify-content: space-evenly;
  width: 100vw;
  align-items: center;
  flex-direction: row;
  position: sticky;
  top: 0;
  z-index: 1111;
}

.image_logo {
  width: 5.6%;
  display: flex;
  flex-direction: column;
}
img {
  padding-top: 10px;
  height: 30px;
}

.image_logo a {
  margin-top: -10px;
}

.image_logo a span {
  font-size: 11px;
  color: white;
  font-style: italic;
}

#exp {
  color: #ffe11b;
  font-weight: bold;
}

.plus_logo {
  height: 1.2rem;
}
.image_logo span:hover {
  text-decoration: underline;
}

.input_sec {
  width: 60%;
  display: flex;
}
.input_sec input {
  padding: 10px;
  width: 95%;
  margin-top: 12px;
  margin-bottom: 12px;
  border: none;
  cursor: text;
}

.header_icon {
  margin-top: 12.4px;
  height: 35px !important;
  background-color: white;
  cursor: pointer;
}

.login_btn {
  background-color: white;
  color: #2874f0;
  font-size: 15px;
  font-weight: bold;
  border: none;
  padding: 7px 35px;
  margin: 12px 0px;
  cursor: pointer;
}
.header_Bicon {
  margin-top: 2px !important;
  height: 35px !important;
  background-color: none;
  color: white;
}

.cart {
  width: 5%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: white;
  font-size: 18px;
  font-weight: 400;
  cursor: pointer;
}

.header_more {
  color: white;
  font-size: 18px;
  font-weight: 400;
  display: flex;
  height: 9vh;
  align-items: center;
}
.list_more {
  display: none;
}

.header_more:hover {
  cursor: pointer;
}
.header_more:hover .list_more {
  position: absolute;
  display: flex;
  flex-direction: column;
  margin-top: 43vh;
  right: 30px;
  list-style: none;
  box-shadow: 2px 2px rgb(226, 226, 226);
  background-color: white;
  color: black;
}

.list_more li {
  padding: 10px 15px;
  box-sizing: border-box;
  border-bottom: 0.1px solid rgb(231, 230, 230);
  font-size: 15px;
}
.list_more li:hover {
  background-color: rgb(243, 243, 243);
  cursor: pointer;
}
.li_icon {
  display: flex;
  align-items: center;
}
.li_icons {
  color: #2874f0;
  transform: scale(0.7);
}
